import jQuery from 'jquery'
import $ from 'jquery'

export function trigger(){
	(function($) {

		"use strict";
	
		var fullHeight = function() {
	
			$('.js-fullheight').css('height', $(window).height());
			$(window).resize(function(){
				$('.js-fullheight').css('height', $(window).height());
			});
	
		};
		fullHeight();
	
		$('#sidebarCollapse').on('click', function () {
		  $('#sidebar').toggleClass('active');
	  });
	
	})(jQuery);
}


